:root {
  /*Color Variables to be used*/
  --darkBlue: rgb(18, 20, 51);
  --black: rgb(0, 0, 0);
  --yellow: #ffdc95;
  --silver: rgb(199, 199, 199);

  /*Intial x and y positions for mountain01*/
  --xPos: -50%;
  --yPos: 65vh;
}

body {
  font-family: monospace;
  background-color: silver;
}

h1 {
  text-align: center;
  color: var(--darkBlue);
}

h2 {
  text-align: center;
  color: var(--darkBlue);
}

h3 {
  text-align: center;
  color: var(--darkBlue);
}

#canvas {
  position: relative;
  display: flex;
  align-self: center;
  /*justify-content: center;*/
  /*align-items: center;*/
  background: var(--darkBlue);
  width: 98.5vw;
  height: 100vh;
  border-radius: 1%;
  overflow: hidden;
}
/*This circle is the Moon itself*/
.circle {
  align-self: center;
  margin-left: 3vw;
  margin-top: -55vh;
  width: 35vmin;
  height: 35vmin;
  border-radius: 50%;
  background: var(--yellow);
}
/*This is the shadow of the moon, it
uses a clip path*/
.shadow {
  background: var(--darkBlue);
  width: 40.5vmin;
  height: 35.55vmin;
  border-radius: 40%;
  clip-path: circle(50% at 10% 50%);
}

.stars {
  background: var(--yellow);
  width: 0.1vw;
  height: 0.1vh;
  position: absolute;
  /*Each individual box-shadow is a star*/
  /*Still need to add more stars*/
  box-shadow: 80vw 10vh 0.1vw 0.65vw var(--yellow),
    60vw 20vh 0.2vw 0.45vw var(--yellow), 38vw 43vh 0.4vw 0.3vw var(--yellow),
    43vw 5vh 0.3vw 0.3vw var(--yellow), 7vw 50vh 0.2vw 0.7vw var(--yellow),
    93vw 60vh 0.4vw 0.25vw var(--yellow), 74.5vw 40vh 0.1vw 0.35vw var(--yellow),
    62vw 63vh 0.2vw 0.15vw var(--yellow);
  border-radius: 50%;
}

.mountain01 {
  width: 1vw;
  height: var(--yPos);
  margin-left: var(--xPos);
  border-left: 40vw solid transparent;
  border-right: 60vw solid transparent;
  border-bottom: 37vh solid var(--black);
}

.mountain02 {
  width: 1vw;
  height: 75vh;
  margin-left: -45vw;
  border-left: 30vw solid transparent;
  border-right: 60vw solid transparent;
  border-bottom: 27vh solid var(--black);
}
